.navbar {
    background-color: black;
    height: 90px;
    display: flex;
    align-items: center;
    justify-content: start;
}
.menu {
    margin-left: 2rem;
    font-size: 2rem;
    background: none;
}
.nav-menu {
    background-color: black;
    width: 300px;
    height: 150vh;
    display: flex;
    position: fixed;
    top: 0;
    left: -100%;
    justify-content: center;
    transition: 850ms;
}
.nav-menu.active {
left: 0;
transition: 350ms;
}

.nav-text {
    display: flex;
    justify-content: start;
    align-items: center;
    padding: 8px 0px 8px 0px;
    list-style: none;
    height: 30px;
}

.nav-text a {
    text-decoration: none;
    font-size: 20px;
    color: white;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 16px;
    border-radius: 5px;
}
.nav-text a:hover {
    background-color: teal;
}
.nav-menu-items {
    width: 100%;
}
.nav-menu-toggle {
    background-color: black;
    width: 100%;
    height: 80px;
    justify-content: start;
    align-items: center;
    display: flex;
}
span {
    margin-left: 10px;
}